.about{position:relative;display:flex;flex-direction:row;height:100vh}.about .slide{position:absolute;top:0;width:100vw;display:flex;align-items:center;place-content:center;opacity:.2;height:100vh}.about .slide h1{right:0;color:#000;font-family:"Gauss"}.about__main{position:absolute;width:100vw;height:100vh;display:flex;align-items:center;gap:20px;text-align:left;top:0;left:0;z-index:10}.about__main .aboutme{width:100%;display:flex;padding:50px 0px;flex-direction:column}@media(max-width: 23.5em){.about__main .aboutme{padding-bottom:30px}}.about__main .aboutme__heading{margin-bottom:30px}@media(max-width: 30em){.about__main .aboutme__heading{margin-bottom:15px}}.about__main .aboutme__heading h1{position:relative;font-family:"Gauss";font-size:3vw;display:flex;align-items:center;padding-left:2px;color:#8b8e38}.about__main .aboutme__heading h1 span{font-size:.8em;font-family:"SF-Mono";font-weight:bold;margin-right:20px;color:#8b8e38}.about__main .aboutme__heading h1::after{content:"";display:block;position:relative;top:-5px;width:20vw;height:1px;margin-left:20px;margin-top:5px;background-color:#8b8e38}@media(max-width: 30em){.about__main .aboutme__heading h1::after{margin-top:7px}}@media(max-width: 30em){.about__main .aboutme__heading h1{font-size:1.4em}}.about__main .aboutme__body{margin-bottom:15px;display:flex;flex-direction:row;justify-content:space-between}.about__main .aboutme__body p{font-size:1em;font-family:"SF-Mono";width:100%;max-width:600px;margin-bottom:15px;line-height:25px}@media(max-width: 30em){.about__main .aboutme__body p{font-size:.9em;line-height:22px}}@media(max-width: 23.5em){.about__main .aboutme__body p{font-size:.7em;line-height:20px}}.about__main .aboutme__body .img{position:relative !important;width:340px;height:340px;margin:auto;overflow:hidden;z-index:1}.about__main .aboutme__body .img img{position:absolute !important;box-sizing:border-box !important;top:0px;left:0px;width:320px;height:320px;padding-top:30px;padding-inline:10px;aspect-ratio:1/1 !important;background-color:#513b08;border:2px solid #513b08;border-radius:.2em !important;z-index:10}.about__main .aboutme__body .img .after{position:absolute;width:320px;height:320px;top:20px;left:20px;border:2px solid #513b08;border-radius:.2em;z-index:5}.about__main .aboutme__bottom{width:100%;max-width:600px;font-size:1.2em;text-align:left}.about__main .aboutme__bottom p{font-family:"SF-Mono";font-size:.9em;font-weight:bold;margin-bottom:20px}@media(max-width: 30em){.about__main .aboutme__bottom p{font-size:.75em}}@media(max-width: 23.5em){.about__main .aboutme__bottom p{font-size:.7em;margin-bottom:15px}}.about__main .aboutme__bottom ul{display:grid;grid-template-columns:repeat(2, auto);gap:8px}.about__main .aboutme__bottom ul li{position:relative;font-size:.85em;font-family:"Exan";font-weight:bold;padding:7.5px 20px}.about__main .aboutme__bottom ul li::before{content:"▹";position:absolute;left:0px;overflow:hidden;color:#8b8e38;font-size:#8b8e38}@media(max-width: 30em){.about__main .aboutme__bottom ul li{font-size:.75em}}@media(max-width: 23.5em){.about__main .aboutme__bottom ul li{font-size:.7em}}@media(max-width: 23.5em){.about__main .aboutme__bottom ul{gap:5px}}@media(max-width: 1024px){.img{display:none}}